body {
  margin: 0;
      font-family: 'Rubik', sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  overflow-x: hidden;
}

html {
  -webkit-text-size-adjust: 100%;
}

@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap');



:root {
  --content-width: 730px;

  --spacing-xs: 8px;
  --spacing-s: 16px;
  --spacing-m: 24px;
  --spacing-l: 32px;
  --spacing-xl: 48px;
  --spacing-xxl: 72px;

  --minigame-borderWidth: 4px;
  --minigame-width: calc(var(--content-width)/2);
  --minigame-width-mobile: calc(100vw - calc(2 * var(--spacing-m)));

  --minigame-card-height: calc(var(--minigame-width)/3);
  --minigame-card-height-mobile: calc(var(--minigame-width-mobile)/3);

  --border-radius: 4px;

  --yellow: #fac832;
}


* {
  text-rendering: optimizeLegibility;
    text-align: left;

}

body.light { 
    color: black;
    background-color: white;
}

body.dark {
    color: white;
    background-color: #121212;
}



